Typography
Basic Layout
  Product Header
  Primary Navigation
  Secondary Panel
  Dashboard Panel
  Main Content
  Footer Navigation
  Single Column
  Breadcrumb
Forms
Inline Editor
Tables
Widgets
Draggable
Collapse
Tabs
Modal
Alert
Badges
Icons
User Badge
Pagination
Progress Bar
*/

html{
  font-size: 14px;
}
body{
  font-size: 14px;
  line-height: $line-height-base;
  overflow-x: hidden;
  color: $body-color;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Typography */
h1, h2, h3, h4,
.h1, .h2, .h3, .h4{
  margin: 1.414rem 0 0.7rem;
  line-height: 1.2;
  font-weight: 600;
  small{
    display: inline-block;
    margin-left: 0.5rem;
    color: $gray-darker;
  }
}
h1, .h1{
  margin-top: 0;
  font-size: 2.827rem;
  line-height: 1.1;
  font-feature-settings : "palt" 1, "trad" 1;
  small{
    font-size: 1.414rem;
  }
}
h2, .h2{
  font-size: 1.999rem;
  small{
    font-size: 1rem;
  }
}
h3, .h3{
  font-size: 1.414rem;
  small{
    font-size: 1rem;
  }
}
h4, .h4{
  font-size: 1rem;
  font-weight: 500;
  small{
    font-size: 0.85rem;
  }
}
p, ul, ol, dl{
    font-size: 1rem;
    margin-top: 0;
    margin-bottom: 1.3rem;
}
ol ol, ol ul, ul ol, ul ul{
  margin-top: 0.5rem;
  margin-bottom: 0;
}
li{
  margin-bottom: 0.5rem;
}
.mt-bg-dark{
    background: $gray-darkest;
    color: $white;
    a{
        color: $gray-lightest;
    }
}


/* Basic Layout */
.mt-develperPreview{
  background: $cobalt;
  font-size:0.8rem; 
  color: $white; 
  text-align:center;
}
/* Product Header */
.mt-productHeader{
  @extend .col, .d-flex, .justify-content-start, .align-items-center;
  padding: 0;
  border-bottom: 1px solid $gray-light;
  background: $white;
}
.mt-productHeader__logo{
  margin: 0;
  padding: 0;
  font-size: 1rem;
  text-decoration: none;
  a{
    color: $body-color;
    text-decoration: none;
  }
  a[role="home"]{
    display: inherit;
  }
  .btn{
    display: block;
    padding: .5rem 0;
    overflow: hidden;
    color: $body-color;
    text-align: left;
    text-overflow: ellipsis;
    white-space: nowrap;
    &:after{
      display: inline-block;
      width: 0;
      height: 0;
      float: right;
      margin-top: 1rem;
      content: "";
      color: $gray;
    }
    img{
      margin-right: .5rem;
      vertical-align: middle;
    }
  }
}
/* Primary Navigation */
.mt-primaryNavigation{
  @extend .col-2;
  padding-bottom: 1rem;
  padding-right: 0;
  padding-left: 0;
  max-width: 240px; 
  background: $gray-lightest;
}
.mt-primaryNavigation--sites{
  @extend .mt-primaryNavigation;
  background: $gray-darker;
}
.mt-primaryNavigation__container{
  display: flex; 
  min-height:100vh; 
  align-items: stretch;
  flex-direction: column;
}
.mt-primaryNavigation__header{
  padding: 0 1rem;
}
.mt-primaryNavigation__control{
  padding: 0.5rem 1rem;
  .btn-default{
    display: block;
    margin-bottom: 0.2rem;
    padding-left: 0.5rem;
    text-align: left;
  }
  .mt-icon{
    fill: $gray-light;
  }
}
.mt-primaryNavigation__info{
  margin-top: 1rem;
}
.mt-primaryNavigation__system{
  @extend .btn, .btn-link;
  display: block;
  padding: 1rem;
  width: 100%;
  border-top: 1px solid $gray-lighter;
  color: $white;
  text-align: left;
  text-decoration: none;
  &:hover{
    border-top: 1px solid $gray-lighter;
    color: $white;
    text-decoration: none;
  }
}
.mt-primaryNavigation__content{
  @extend .flex-items-1;
  overflow: auto;
}
.mt-primaryNavigation__sites{
  list-style: none;
  padding: 0 1rem;
  a{
    display: block;
    padding: 0.2rem 0;
    color: $white;
    text-decoration: none;
  }
  ul{
    margin-left: 1rem;
  }
}
.mt-primaryNavigation__list{
  margin: 0;
  padding: 0;
  list-style: none;
}
.mt-primaryNavigation__item{
  margin: 0;
  a{
    display: block;
    padding: 0.8rem 1rem;
    color: $body-color;
    line-height: 1;
    text-decoration: none;
    cursor: pointer;
  }
  & > a{
    border-bottom: 1px solid $gray-lighter;
    &:after{
          display: inline-block;
          width: 0;
          height: 0;
          float: right;
          margin-top: .5rem;
          content: "";
          color: $gray;
    }
  }
}
.mt-productHeader__logo, 
.mt-primaryNavigation__item{ 
  button, a, 
  button:hover, a:hover{
    text-decoration: none;
  }
  & > button[aria-expanded="false"], 
  & > a[aria-expanded="false"]{
    .mt-icon{
      fill: $gray-dark;
    }
    &:after{
      margin-left: .4rem;
      border-top: .4rem solid;
      border-right: .4rem solid transparent;
      border-left: .4rem solid transparent;
      border-bottom: 0;
    }
  }
  & > button[aria-expanded="true"], 
  & > a[aria-expanded="true"]{
    .mt-icon{
      fill: $gray-darkest;
    }
    &:after{
      margin-left: .4rem;
      border-top: 0;
      border-right: .4rem solid transparent;
      border-left: .4rem solid transparent;
      border-bottom: .4rem solid;
    }
  }
}
.mt-primaryNavigation__item{
  .mt-icon{
    margin-right: 0.5rem;
    fill: $gray-dark;
  }
}
.mt-primaryNavigation__child{
  margin: 0;
  padding: 0;
  border-bottom: 1px solid $gray-lighter;
  list-style: none;
  li{
    margin: 0;
  }
  a{
    padding-left: 2.7rem;
  }
  a:hover{
    background: $gray-lighter;
  }
  .selected{
    background: $white;
    font-weight: $font-weight-bold;
  }
}
/* Secondary Panel */
.mt-secondaryPanel{
  @extend .col-3;
  padding-bottom: 4rem;
  padding-top: 1rem;
  max-width: 400px; 
  background: $gray-lightest;
}
/* Dashboard Panel */
.mt-dashboardPanel{
  @extend .col-2;
  padding-right: 0;
  padding-left: 0;  
  padding-bottom: 4rem;
  max-width: 320px; 
  background: $gray-lightest;
}
/* Main Content */
.mt-mainContent{
  @extend .col;
  padding-bottom: 4rem;
  padding-left: 2rem;
  padding-right: 2rem;
}
/* Footer Navigation */
.mt-footer{
  @extend .col;
  padding: 1rem;
  border-top: 1px solid $gray-light;
  .list-inline{
    margin-bottom: 0.5rem;
  }
  .list-inline-item{
    margin-bottom: 0;
    margin-right: 1rem;
  }
}
/* Single Column */
.container-single-column{
  @extend .container-fluid;
  display: flex; 
  min-height:100vh; 
  flex-direction: column;
}
.flex-items-1{
  flex: 1;
}
/* Breadcrumb */
.mt-breadcrumb{
  margin: 0 0 1rem 0;
  padding: 0.5rem 1rem;
  border: 1px solid $gray-lighter;
  border-radius: $border-radius;
  font-size: 0.8rem;
  a, a:hover{
    color: $body-color;
    text-decoration: none;
  }
}
.mt-gts{
  margin: 0 0.5rem;
  color: $black;
  font-size: 1rem;
}

/* Forms */
fieldset.form-group{
  margin-bottom: 2rem;
}
.form-control{
  option{
    padding: 2px 4px;
  }
}
.btn{
    cursor: pointer;
    font-weight: $font-weight-bold;
    text-decoration: none;
    .mt-icon{
      margin-right: 0.2rem;
    }
}
.btn-default{
  border: 1px solid $gray;
  background: $white;
  font-weight: $font-weight-normal;
  &:hover{
    background: $gray-lightest;
  }
}
.btn-group-space{
    .btn + .btn,
    .btn + .btn-group,
    .btn-group + .btn,
    .btn-group + .btn-group {
        margin-left: 0.25rem;
    }
}
.custom-control-indicator{
  border: 1px solid $gray;
}
.custom-control-input:checked + .custom-control-indicator{
    border-color: transparent;
}
.mt-switch{
  height: 0;
  width: 0;
  visibility: hidden;
  + label {
    position: relative;
    width: 3rem;
    height:  20px;
    background: $gray;
    display: inline-block;
    border-radius: 10px;
    cursor: pointer;
    text-indent: -9999px;
    &:after {
        content: '';
        position: absolute;
        top: 1px;
        left: 1px;
        width: 18px;
        height: 18px;
        background: $white;
        border-radius: 9px;
        transition: 0.3s;
      }
  }
}
.mt-switch:checked{
  + label {
    background: $lochmara500;
    &:after {
      left: calc(100% - 1px);
      transform: translateX(-100%);
    }
    &:active:after {
      width: 18px;
    }
  }
}
.mt-stepper{
  text-align: center;
}

/* Inline Editor */
.mt-editor{
  display: inline-block;
  padding: 0.3rem 1rem;
  background: $black;
  border-radius: $border-radius;
  color: $white;
  .btn{
    padding: 0.2rem 0.5rem;
  }
  .btn-link{
    color: $white;
  }
  a{
    display: block;
    padding: 0.2rem 0.2rem;
    color: $white;
    text-decoration: none;
    &:hover{
      background: $gray-darker;
    }
  }
  .dropdown-menu{
    min-width: auto;
    background: $black;
    border-radius: $border-radius;
    color: $white;
    a{
      padding: 0.2rem 1rem;
    }
    ul{
      margin: 0;
      padding: 0;
      list-style: none;
      li{
        display: list-item;
        margin: 0 0 0.3rem 0;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
}
.mt-editor__list{
  margin: 0;
  padding: 0;
  list-style: none;
  li{
    display: inline-block;
    margin: 0 0.5rem 0 0;
    &:last-child{
      margin-right: 0;
    }
  }
}

/* Tables */
.mt-table{
  th, td{
    padding: .8rem;
    label{
      margin-bottom: 0;
    }
    .custom-control-description{
      margin-left: 0.5rem;
    }
    &.mt-table__control{
      width: 1.2rem;
    }
    &[scope="row"]{
      padding-right: 0;
    }
  }
  thead th{
    background: $gray-lightest;
    border-top: 0;
    border-bottom: 1px solid $gray;
    color: $gray-darkest;
    font-weight: $font-weight-bold;
    white-space: nowrap;
  }
  td{
    border-bottom: 1px solid $gray-lighter;
    word-break: break-all;
    word-wrap: break-word;
  }
  tbody tr:last-child td{
    border-bottom: 0;
  }
  .nowrap{
    word-break: normal; 
    word-wrap: normal;
  }
  .custom-control{
    margin-right: 0;
  }
}
.mt-table__hierarchy{
  padding: 0.8rem 0 0 0;
  > *:last-child{
    margin-bottom: 0;
  }
}
.mt-table__highlight{
  background: $lochmara50;
}
.mt-table__sort{
  display: block;
  color: $body-color;
  text-decoration: none;
  &:after{
    display: inline-block;
    height: 12px;
    margin-left: 0.4rem;
    width: 10px;
    vertical-align: text-top;
  }
  &:hover, &:visited, &:active{
    color: $body-color;
    text-decoration: none;
  }
}
.mt-table__descend{
  @extend .mt-table__sort;
  &:after{
    content: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2211%22%20height%3D%2212%22%20viewBox%3D%220%200%2011%2012%22%3E%0A%20%20%3Cpath%20fill%3D%22%232F2F2F%22%20fill-rule%3D%22evenodd%22%20d%3D%22M4.492%209.386V.326c0-.18.157-.326.38-.326h.763c.223%200%20.382.145.382.325v9.053l3.293-3.14c.15-.143.39-.143.54%200l.538.513c.15.143.15.373%200%20.515l-4.852%204.628c-.08.075-.183.11-.286.107-.103.004-.207-.032-.286-.107L.112%207.265c-.15-.142-.15-.372%200-.514l.54-.513c.148-.142.39-.142.538%200l3.302%203.15z%22/%3E%0A%3C/svg%3E");
  }
}
.mt-table__ascend{
  @extend .mt-table__sort;
  &:after{
    content: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2211%22%20height%3D%2212%22%20viewBox%3D%220%200%2011%2012%22%3E%0A%20%20%3Cpath%20fill%3D%22%232F2F2F%22%20fill-rule%3D%22evenodd%22%20d%3D%22M6.008%202.614v9.06c0%20.18-.157.326-.38.326h-.763c-.223%200-.382-.145-.382-.325V2.622L1.19%205.762c-.15.143-.39.143-.54%200L.113%205.25c-.15-.143-.15-.373%200-.515L4.964.107c.08-.075.183-.11.286-.107.103-.004.207.032.286.107l4.852%204.628c.15.142.15.372%200%20.514l-.54.513c-.148.142-.39.142-.538%200l-3.302-3.15z%22/%3E%0A%3C/svg%3E");
  }
}

/* Widgets */
.mt-widget{
  @extend .card;
  margin-bottom: 2rem;
  border: 1px solid $gray-light;
  border-radius: $border-radius;
  background: transparent;
  .mt-widget__list li:last-child{
    border-bottom: 0;
  }
}
.mt-widget__title{
  margin: 0;
  padding: 1rem;
  border-bottom: 1px solid $gray-light;
  font-size: 1rem;
  font-weight: $font-weight-bold;
}
.mt-widget__content{
  padding: 1rem;
  *:last-child{
    margin-bottom: 0;
  }
}
.mt-widget__list{
  padding: 0;
  list-style: none;
  li{
    padding: 1rem 0 1.2rem;
    border-bottom: 1px solid $gray-light;
  }
}
.mt-widget__latest{
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid $gray-lighter;
  li{
    padding: 0.2rem 0;
  }
}
.mt-widget__sitename{
  margin: 0 0 1rem 0;
}
.mt-widget__url{
  margin: 0 0 1rem;
  font-family: $font-family-monospace;
}
.mt-widget__sitelist{
  display: inline-block;
  line-height: 2;
}
.mt-widget__contenttype{
  padding: 0.5rem 0;
  border-bottom: 1px solid $gray-light;
}
.mt-widget__info{
  padding-bottom: 0.2rem;
  color: $gray-darkest;
  font-size: 0.85rem;
}
.mt-widget--panel{
  @extend .mt-widget;
  border: 0;
  border-bottom: 1px solid $gray-light;
  .mt-widget__title{
    padding: 0.5rem 1rem;
    border-bottom: 0;
  }
  .mt-widget__content{
    padding: 0.5rem 1rem 1rem;
  }
  .mt-widget__list li:last-child{
    border-bottom: 1px solid $gray-light;
  }
}
.mt-widget--site{
  margin-bottom: 1rem;
  padding: 1rem 1rem 2rem;
  border-bottom: 1px solid $gray-light;
  *:last-child{
    margin-bottom: 0;
  }
  &:last-child{
    margin-bttom: 0;
    padding-bottom: 1rem;
    border-bottom: 0;
  }
  .col .row{
    margin-left: 0;
    margin-right: 0;
  }
}
.mt-widget__data{
  @extend .col;
  border-right: 1px solid $gray-lighter;
  font-size: 0.85rem;
  text-align: center;
  &:last-child{
    border-right: 0;
  }
  strong{
    font-size: 1.25rem;
    &.number{
      font-famly: $font-family-numbers;
      font-size: 2rem;
      letter-spacing: -0.05rem;
    }
  }
}

/* Draggable */
.mt-draggable{
  @extend .d-flex, .no-gutters;
  padding: 0.5rem 0.5rem 0.5rem 0;
  border: 1px solid $gray;
  border-radius: $border-radius;
  cursor: move;
  .mt-icon, .mt-icon--secondary{
    margin: 0 .5rem 0 0;
  }
  &[aria-grabbed="true"]{
    border-color: theme-color("info");
    font-weight: $font-weight-bold;
  }
}

/* Collapse */
.mt-collapse{
  padding: 0.5rem 0 0.5rem 1rem;
  border: 1px solid $gray;
  border-radius: $border-radius;
}
.mt-collapse__container{
  @extend .d-flex, .no-gutters;
}
.mt-collapse__content{
  padding: 1rem;
  *:last-child{
    margin-bottom: 0;
  }
}
.mt-collapse__blockset{
  @extend .mt-collapse__content;
  padding: 0;
}
.mt-collapse__block{
  padding: 1rem;
  border-bottom: 1px solid $gray-light;
  &:last-of-type{
    border-bottom: 0;
  }
}
.mt-collapse__block--selected{
  background: $lochmara50;
}
.mt-collapse--contentblock{
  @extend .mt-collapse;
  border: 0;
  border-bottom: 1px solid $gray;
  .mt-collapse__container{
    padding-bottom: 0.5rem;
  }
  .mt-icon, .mt-icon--secondary, .mt-icon--contentblock{
    margin: 0 .5rem 0 0;
    &.mt-icon--sm{
      margin: 0;
    }
  }
  .mt-ic_move{
    cursor: move;
  }
}
.mt-archivemapping, 
.mt-pluginpanel{
  padding: 1rem 0 1rem 1rem;
  border-bottom: 1px solid $gray;
  background: $gray-lightest;
  &:first-of-type{
    border-top: 1px solid $gray;
  }
  &.mt-archivemapping--priority{
    background: $lochmara50;
  }
  .mt-switch+label{
    margin-right: 1rem;
  }
  .custom-control-description{
    font-weight: $font-weight-bold;
  }
}
.mt-collapse, .mt-archivemapping, .mt-pluginpanel{
  .mt-icon--secondary{
    margin: 0 .5rem;
    transition: 0.2s ease-in-out;
  }
  .mt-icon--secondary.mt-pluginpanel__icon{
    margin: 0 0.2rem 0 0; 
  }
  a[aria-expanded="false"] .mt-icon--secondary{
    transform: rotate(180deg); 
  }
  a[aria-expanded="true"] .mt-icon--secondary{
    transform: rotate(0deg); 
  }
}

/* Tabs */
.mt-tabs{
  margin-bottom: 1rem;
  border-bottom: 1px solid $gray-light;
  li{
    margin-bottom: 0;
  }
  .nav-link{
    border-bottom: 4px solid transparent;
    color: $gray-darker;
    text-decoration: none;
    &:hover{
      color: $black;
    }
  }
  .nav-link.active{
    border-bottom: 4px solid $lochmara;
    color: $black;
  }
}

/* Modal */
.modal-title{
  margin-top: 0;
}

/* Alert */
.alert{
    h1, h2, h3, h4,
    .h1, .h2, .h3, .h4{
        margin-top: 0;
    }
}

/* Badges */
.badge{
  padding: 0.4rem 0.5rem;
  border: 1px solid;
  border-radius: 2rem;
  background: $white;
  font-size: 85%;
}
.badge-default{
  border-color: $gray;
  color: $gray-darker;
}
.badge-primary{
  border-color: theme-color("primary");
  color: theme-color("primary");
}
.badge-success{
  border-color: theme-color("success");
  color: theme-color("success");
}
.badge-info{
  border-color: theme-color("info");
  color: theme-color("info");
}
.badge-warning{
  border-color: theme-color("warning");
  color: theme-color("warning");
}
.badge-danger{
  border-color: theme-color("danger");
  color: theme-color("danger");
}
h1, h2,
.h1, .h2{
    .badge{
        font-size: 50%;
        vertical-align: middle;
    }
}
h3, .h3{
  .badge{
    font-size: 60%;
    vertical-align: middle;
  }
}

/* Icons */
.close{
  line-height: 0.5;
  opacity: 0.8;
}
.mt-icon{
  position: relative;
  height: 24px;
  top: -1px;
  width: 24px;
  fill: $gray-darker  ;
  vertical-align: middle;
}
.mt-icon--sm{
  height: 16px;
  width: 16px;
}
.mt-icon--lg{
  height: 32px;
  width: 32px;
}
.mt-icon--xlg{
  height: 40px;
  width: 40px;
}
.mt-icon--primary{
  @extend .mt-icon;
  fill: theme-color("primary");
}
.mt-icon--secondary{
  @extend .mt-icon;
  fill: theme-color("secondary");
}
a:hover .mt-icon--secondary{
  fill: $gray-darker;
}
.mt-icon--success{
  @extend .mt-icon;
  fill: theme-color("success");
}
.mt-icon--info{
  @extend .mt-icon;
  fill: theme-color("info");
}
.mt-icon--warning{
  @extend .mt-icon;
  fill: theme-color("warning");
}
.mt-icon--danger{
  @extend .mt-icon;
  fill: theme-color("danger");
}
.mt-icon--inverse{
  @extend .mt-icon;
  fill: $white;
}
.mt-icon--contentblock{
  display: inline-block;
  height: 24px;
  width: 24px;
  border: 1px solid $gray;
  border-radius: 50%;
  background: $white;
  text-align: center;
  vertical-align: middle;
}

/* User Badge */
.mt-user{
  display: inline-block;
  position: relative;
  .mt-icon--sm, .mt-icon--img{
    display: block;
    margin: 2px auto;
  }
}
.mt-user__badge{
  height: 22px;
  width: 22px;
  bottom: 0;
  right: 0;
  position: absolute;
  border: 2px solid $white;
  border-radius: 10px;
  background: theme-color("secondary");
}
.mt-user__badge--img{
  @extend .mt-user__badge;
  background: $white;
}
.mt-user__badge--primary{
  @extend .mt-user__badge;
  background: theme-color("primary");
}
.mt-user__badge--success{
  @extend .mt-user__badge;
  background: theme-color("success");
}
.mt-user__badge--danger{
  @extend .mt-user__badge;
  background: theme-color("danger");
}

/* Pagination */
.pagination, .pagination-sm, .pagination-lg{
    border-radius: 0;
    .page-item {
        &:first-child, &:last-child{
            .page-link {
                border-radius: 0;
            }
        }
    }
}
.page-link{
    border-color: transparent;
    text-decoration: none;
}
.page-item {
    margin-right: 1rem;
    &:first-child {
        .page-link {
            border: 1px solid $gray;
            border-radius: 0;
            font-weight: 600;
            &:before{
                content: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%225%22%20height%3D%229%22%20viewBox%3D%220%200%205%209%22%3E%0A%20%20%3Cpath%20fill%3D%22%233269B4%22%20fill-rule%3D%22evenodd%22%20d%3D%22M5%200v9L0%204.5%22/%3E%0A%3C/svg%3E");
                display: inline-block;
                height: 5px;
                margin-right: 0.4rem;
                width: 9px;
            }
        }
    }
    &:last-child {
        .page-link {
            margin-right: 0;
            margin-left: 0;
            border: 1px solid $gray;
            border-radius: 0;
            font-weight: 600;
            &:after{
                content: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%225%22%20height%3D%229%22%20viewBox%3D%220%200%205%209%22%3E%0A%20%20%3Cpath%20fill%3D%22%233269B4%22%20fill-rule%3D%22evenodd%22%20d%3D%22M0%200v9l5-4.5%22/%3E%0A%3C/svg%3E");
                display: inline-block;
                height: 5px;
                margin-left: 0.4rem;
                width: 9px;
            }
        }
    }
      &.first-last   .page-link {
        color: $gray-darker;
        background-color: $gray-lightest;
        border-color: $gray-lighter;
    }
}

/* Progress Bar */
.mt-progress{
  border: 1px solid $gray-light;
  border-radius: $border-radius;
  .progress{
    margin: 1px;
  }
}
